.header{
    display: flex;
    justify-content: space-between;
    flex: 1;
    padding: 12px 0px;
    position: relative;
    align-items: center;
    width: 100%;
}
.menu{
    display: flex;
    flex: 1;
    justify-content: flex-end;
}
.menu-icon{
    font-size: 24px;
}
.mobile-menu{
    display: none;
}
.web-menu{
    display: flex;
}
.logo{
    display: flex;
    font-size: 36px;
    color: blueviolet;
    font-weight: 600;
}

@media only screen and (max-width: 720px) {
    .mobile-menu{
        display: flex;
    }
    .web-menu{
        display: none;
    }
    .logo{
        font-size: 24px;
        font-weight: 600;
    }
}